import React from 'react';
import L from 'leaflet';
import { Map, Marker, TileLayer, LayersControl, LayerGroup } from 'react-leaflet';

import PopupGeneral from '../popup/PopupGeneral';

import 'leaflet/dist/leaflet.css'; // Solve this styles import for Leaflet another way in future!
import './MainMap.css';

// import './leaflet.css';

const position = [49.00, 32.00];
const zoomLevel = 8;
const { Overlay } = LayersControl;

function MainMap() {
  return (
    <Map
      center={position}
      zoom={zoomLevel}
    >
      <TileLayer
        url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
      />
      <LayersControl>
        <Overlay name="Custom marker">
          <Marker position={position}>
            <PopupGeneral />
          </Marker>
        </Overlay>
      </LayersControl>

    </Map>
  );
}

export default MainMap;
